<template>
    <div class="food_container">
        <header id="head_top">
            <section class="head_goback" @click="goBack">
                <i class="el-icon-arrow-left"></i>
            </section>
            <section class="title_head ellipsis">
                <span class="title_text">
                    {{this.$route.query.title}}
                </span>
            </section>
        </header>
        <section class="sort_container">
        <div class="sort_item choose_type">
            <div class="sort_item_container">
                <el-button @click="show1 = !show1;show2 = false;show3 = false;" class="sort_item_border">
                    <span v-if="!show1" class="sp">{{this.$route.query.title}}</span>
                    <i class="el-icon-caret-bottom" v-if="!show1"></i>
                    <transition>
                        <span>
                            <span v-if="show1">分类</span>
                            <i class="el-icon-caret-top" v-if="show1"></i>
                        </span>
                    </transition>
                </el-button>
                <div style=" width: 350px">
                    <el-collapse-transition>
                        <div v-show="show1" style="width: 340px;">
                                <div class="transition-box" >
                                        <section class="category_left">
                                            <ul class="">
                                                <li class="category_left_li" v-for="(cate,index) in options" @click="addClass(index,$event,cate.sub_categories)" v-bind:class="{bgc:index == current}">
                                                    <section class="">
                                                        <img :src="'https://fuss10.elemecdn.com/'+cate.image_url" alt="" class="category_icon">
                                                        <span>{{cate.name}}</span>
                                                    </section>
                                                    <section>
                                           <span class="category_count">
                                               {{cate.count}}
                                           </span>
                                                        <i class="el-icon-arrow-right"></i>
                                                    </section>
                                                </li>
                                            </ul>
                                        </section>
                                    <section class="category_right">
                                        <ul  style="height: 16rem;overflow: scroll" >
                                            <li class="category_right_li"  v-for="pro in categories" @click="searchShop(pro)">
                                                <span>{{pro.name}}</span>
                                                <span>{{pro.count}}</span>
                                            </li>
                                        </ul>
                                    </section>
                                </div>

                        </div>

                    </el-collapse-transition>
                </div>
            </div>
        </div>
            <div class="sort_item choose_type">
                <div class="sort_item_container">
                    <el-button  @click="rotateE1">排序
                        <i class="el-icon-caret-bottom"  id="two"  ></i>
                    </el-button>
                    <div style=" margin-top:.4rem;width:16rem;margin-left:-5.28rem">
                        <el-collapse-transition>
                            <div v-show="show2">
                                <div class="transition-box">
                                    <section class="sort_detail_type">
                                        <ul class="sort_list_container" id="maskOrder">
                                        <li class="sort_list_li" @click="sortT(4,1)">
                                        <i class="el-icon-sort"></i>
                                        <p>
                                            <span>智能排序</span>
                                            <i class="el-icon-check" style="display: none;"></i>
                                        </p>

                                    </li>
                                        <li class="sort_list_li"  @click="sortT(5,2)">
                                            <i class="el-icon-location-outline"></i>
                                            <p>
                                                <span>距离最近</span>
                                                <i class="el-icon-check"  style="display: none;"></i>
                                            </p>
                                        </li>
                                        <li class="sort_list_li"  @click="sortT(6,3)">
                                            <i class="el-icon-sunny" ></i>
                                            <p>
                                                <span>销量最高</span>
                                                <i class="el-icon-check" style="display: none;"></i>
                                            </p>
                                        </li>
                                        <li class="sort_list_li"  @click="sortT(1,4)">
                                            <i class="el-icon-money"></i>
                                            <p>
                                                <span>起送价最低</span>
                                                <i class="el-icon-check"  style="display: none;"></i>
                                            </p>
                                        </li>
                                        <li class="sort_list_li"  @click="sortT(2,5)">
                                            <i class="el-icon-star-off" ></i>
                                            <p>
                                                <span>配送速度最快</span>
                                                <i class="el-icon-check"  style="display: none;"></i>
                                            </p>
                                        </li>
                                        <li class="sort_list_li"  @click="sortT(3,6)">
                                            <i class="el-icon-star-off"></i>
                                            <p>
                                                <span>评分最高</span>
                                                <i class="el-icon-check" style="display: none;"></i>
                                            </p>
                                        </li>
                                    </ul>
                                    </section>
                                </div>
                                <!--<div class="transition-box">el-collapse-transition</div>-->
                            </div>
                        </el-collapse-transition>
                    </div>
                </div>
            </div>
            <div class="sort_item choose_type">
            <div class="sort_item_container">
                    <el-button  @click="rotateE">筛选
                            <i class="el-icon-caret-bottom"  id="one"  ></i>
                    </el-button>

                    <div style="margin-top: 5px;width:16rem;margin-left:-10.65rem">
                        <el-collapse-transition>
                            <div v-show="show3">
                                <div class="transition-box">
                                    <header class="filter_header_style">配送方式</header>
                                    <ul class="filter_ul">
                                        <li class="filter_li" v-for="(filter,index) in delivery_modes" @click="changePic(index)">
                                            <i class="el-icon-bicycle" v-if="picShow"></i>
                                            <i class="el-icon-check" v-else></i>
                                            <span>{{filter.text}}</span>
                                        </li>
                                    </ul>
                                </div>
                                <div class="transition-box">
                                    <header class="filter_header_style">商家属性(可以多选)</header>
                                    <ul class="filter_ul" id="checkboxLi">
                                        <li class="filter_li" v-for="(activity,index) in activityA" @click="changePic1(activity,index+1)">

                                            <span class="filter_icon">{{activity.icon_name}}</span>
                                            <i class="el-icon-check" style="display: none;"></i>
                                            <span>{{activity.name}}</span>
                                        </li>
                                    </ul>
                                </div>
                                <div class="transition-box">
                                    <footer class="confirm_filter">
                                        <div class="clear_all  filter_button_style" @click="clear()">清空</div>
                                        <div class="confirm_select filter_button_style" @click="confirmBtn">确定{{count}}</div>
                                    </footer>
                                </div>
                            </div>
                        </el-collapse-transition>
                    </div>
                </div>
            </div>
        </section>
        <div class="shop_list_container">
            <div class="shoplist_container">
                <ul >
                    <li class="shop_li" v-for="shop in shopArr" @click="shopP(shop)">
                        <section>
                            <img :src="'https://elm.cangdu.org/img/'+shop.image_path" alt="" class="shop_img">
                        </section>
                        <hgroup class="shop_right">
                            <header class="shop_detail_header">
                                <h4 class="shop_title ellipsis">
                                    <span class="span1">品牌</span>                                              <span>{{shop.name}}</span>
                                </h4>
                                <ul class="shop_detail_ul">
                                    <li v-for="pro in shop.supports" class="support">{{pro.icon_name}}</li>
                                </ul>
                            </header>
                            <h5 class="rating_order_num">
                                <section class="rating_order_num_left">
                                    <section class="rating_section">
                                        <div class="rating_container">
                                            <el-rate
                                                    v-model="shop.rating"
                                                    disabled
                                                    text-color="#ff9900"
                                                    class="start"
                                            >
                                            </el-rate>
                                        </div>
                                        <span class="rating_num">{{shop.rating}}</span>
                                        <section class="order_section">
                                            月销:{{shop.recent_order_num}}
                                        </section>
                                    </section>
                                </section>
                                <section class="rating_order_num_right">
                                    <span class="delivery_left delivery_style">{{shop.delivery_mode?shop.delivery_mode.text:''}}</span>
                                    <span class="delivery_right delivery_style">{{shop.supports[1]?shop.supports[1].name:''}}</span>
                                </section>
                            </h5>
                            <h5 class="fee_distance">
                                <p class="fee">
                                    {{"￥"+shop.float_minimum_order_amount+"起送"}}
                                    <span>/</span>
                                    {{shop.piecewise_agent_fee.tips}}
                                </p>
                                <p class="distance_time">
                                    <span>{{shop.distance}}</span>
                                    <span class="segmentation">/</span>
                                    <span class="order_time">{{shop.float_minimum_order_amount}}小时{{shop.order_lead_time}}</span>
                                </p>
                            </h5>
                        </hgroup>

                    </li>
                </ul>
            </div>
        </div>
    </div>

</template>

<script>
    import $ from 'jquery'
    export default {
        name: "Food",
        data(){
            return{
                delivery_mode:null,
                restaurant_category_ids:[],
                restaurant_category_id:"",
                searchShopArr:[],
                latitude:"",
                longitude:"",
                shopArr:[],
                category:[],
                options:[],
                show:true,
                show1:false,
                show2:false,
                show3:false,
                isShow:true,
                current:0,
                isShow2:false,
                categories:[{name:"全部异国料理"},{name:"日韩料理"},{name:"西餐"},{name:"披萨意面"},{name:"东南亚菜"}],
                // abc:[1,3,4,6,7,9]
                addRotate:false,
                delivery_modes:[],
                activityA:[],
               checkShow:false,
                color:false,
                picShow:true,
                i:0,
                j:0,
                w:0,
                selectArr:[],
                arr:[],
                count:"",
                order_by:"",
                n:0,
                geohash:""
            }
        },
        created(){
            this.latitude = this.$route.query.latitude;
            this.longitude = this.$route.query.longitude;
            this.geohash=this.$route.query.geohash;
            console.log(this.geohash)
            //获取店铺信息
            this.axios.get("shopping/restaurants",{
                params:{
                    latitude:this.$route.query.latitude,
                    longitude:this.$route.query.longitude
                }
            }).then(res=>{
                console.log(res);
                this.shopArr = res;
            }).catch(err=>{})
            //获取分类信息
            this.axios.get("shopping/v2/restaurant/category",{
            }).then(res=>{
                // console.log(res);
                this.options = res;
                this.categoryFun(res)
            }).catch(err=>{})
            //配送方式
            this.axios.get("shopping/v1/restaurants/delivery_modes",{
            }).then(res=>{
                // console.log(res);
                this.delivery_modes = res;
            }).catch(err=>{})
            //商家属性
            this.axios.get("shopping/v1/restaurants/activity_attributes",{
            }).then(res=>{
                // console.log(res);
                this.activityA = res;
            }).catch(err=>{})
        },
        methods:{
            goBack(){
                this.$router.push({
                    path:'/msite',
                })
            },
            addClass(index,event,categories,id){
                this.current = index;
                var el = event.currentTarget;
                this.categories = categories
                console.log(categories);
                if (index == 0){
                    this.show1 = false;
                }
                // this.restaurant_category_ids.push(id) ;
            },
            shopP(shop){
                console.log(shop.id)
                this.$router.push({
                    path:"/shop",
                    query:{
                        shopid:shop.id,
                        latitude:shop.latitude,
                        longitude:shop.longitude,
                        shop:shop,
                        geohash:this.geohash,
                    }
                })
            },
            rotateE(){
                this.i++
                this.show3 = !this.show3;
                this.show1 = false;
                this.show2 = false;
                if (this.i%2){
                    $("#one").css('transform','rotateZ(180deg)')
                } else {
                    $("#one").css('transform','rotateZ(360deg)')
                }

            },
            rotateE1(){
                this.j++
                this.show2 = !this.show2;
                this.show1 = false;
                this.show3 = false;
                if (this.j%2){
                    $("#two").css('transform','rotateZ(180deg)')
                } else {
                    $("#two").css('transform','rotateZ(0deg)')
                }
            },
            sortT(id,i){
                $("#maskOrder>li:nth-of-type(" + this.n + ") p span:nth-of-type(1)").css("color","#666")
                $("#maskOrder>li:nth-of-type(" + i + ") p span:nth-of-type(1)").css("color","#3190e8")
                $("#maskOrder>li:nth-of-type(" + this.n + ")>p>i:nth-of-type(1)").css("display","none")
                $("#maskOrder>li:nth-of-type(" + i + ")>p>i:nth-of-type(1)").css("display","block")
                this.n = i
                // console.log(id);
                this.order_by = id
                //获取店铺信息
                this.axios.get("shopping/restaurants"+'?'+this.latitude+'&'+this.longitude+'&'+this.restaurant_category_id+'&'+this.order_by,{
                    params:{
                        latitude:this.latitude,
                        longitude:this.longitude,
                        restaurant_category_id:id,
                        order_by:this.order_by
                    }
                }).then(res=>{
                    // console.log(res);
                    this.shopArr = res;
                    this.show2 = false;
                }).catch(err=>{})
            },
            changePic(index){
                this.picShow = !this.picShow;
                if (this.w%2){
                    this.count--
                    this.delivery_mode = "";
                } else {

                    this.count++
                    this.delivery_mode = 1;
                }
                this.w++
            },
            clear(){
                this.arr = [];
                this.picShow = !this.picShow
                // console.log("222");
                $("#checkboxLi>li i:nth-of-type(1)").css(
                    "display",
                    "none"
                );
                $("#checkboxLi>li span:nth-of-type(1)").css(
                    "display",
                    "block"
                );
                $("#checkboxLi>li span:nth-of-type(2)").css(
                    "color",
                    "#666"
                );
                this.count = "";

            },
            changePic1(i,a){
                if ($("#checkboxLi>li:nth-of-type(" + a + ") span:nth-of-type(1)").css("display") != "none"
                ) {$("#checkboxLi>li:nth-of-type(" + a + ") span:nth-of-type(2)").css("color",
                    "#409EFF");
                    $("#checkboxLi>li:nth-of-type(" + a + ") span:nth-of-type(1)").css(
                        "display",
                        "none"
                    );
                    $("#checkboxLi>li:nth-of-type(" + a + ") i:nth-of-type(1)").css(
                        "display",
                        "block"
                    );
                    this.count++;
                    if (a == 1) {
                        this.arr.push("8");
                    }else if (a == 2) {
                        this.arr.push("7");
                    }else if (a == 3) {
                        this.arr.push("9");
                    }else if (a == 4) {
                        this.arr.push("5");
                    }else if (a == 5) {
                        this.arr.push("3");
                    }else if (a == 6) {
                        this.arr.push("4");
                    };
                } else {
                    $("#checkboxLi>li:nth-of-type(" + a + ") span:nth-of-type(2)").css(
                        "color",
                        "#666666"
                    );
                    $("#checkboxLi>li:nth-of-type(" + a + ") span:nth-of-type(1)").css(
                        "display",
                        "block"
                    );
                    $("#checkboxLi>li:nth-of-type(" + a + ") i:nth-of-type(1)").css(
                        "display",
                        "none"
                    );
                    this.count--;
                    if (a == 1) {
                        this.arr.splice(0,1);
                    }else if (a == 2) {
                        this.arr.splice(1,1);
                    }else if (a == 3) {
                        this.arr.splice(2,1);
                    }else if (a == 4) {
                        this.arr.splice(3,1);
                    }else if (a == 5) {
                        this.arr.splice(4,1);
                    }else if (a == 6) {
                        this.arr.splice(5,1);
                    };
                }
                console.log(this.arr);
            },
            searchShop(cate){
                // console.log(cate);
                //获取分类店铺信息
                this.axios.get("shopping/restaurants?latitude="+this.latitude+'&longitude='+this.longitude+'&restaurant_category_ids[]='+cate.id).then(res=>{
                    console.log(res);
                    this.shopArr = res;
                    this.show1 = false;
                }).catch(err=>{})
            },
            confirmBtn(){
                if (this.arr.length==0){
                    //获取筛选店铺信息
                    this.axios.get("shopping/restaurants?latitude="+this.latitude+'&longitude='+this.longitude+'&delivery_mode[]='+this.delivery_mode).then(res=>{
                        console.log(res);
                        this.shopArr = res;
                        this.show1 = false;
                    }).catch(err=>{})
                } else if (this.arr.length==1){
                    //获取筛选店铺信息
                    this.axios.get("shopping/restaurants?latitude="+this.latitude+'&longitude='+this.longitude+'&delivery_mode[]='+this.delivery_mode+'&support_ids[]='+this.arr[0]).then(res=>{
                        console.log(res);
                        this.shopArr = res;
                        this.show1 = false;
                    }).catch(err=>{})
                } else if (this.arr.length==2){
                    //获取筛选店铺信息
                    this.axios.get("shopping/restaurants?latitude="+this.latitude+'&longitude='+this.longitude+'&delivery_mode[]='+this.delivery_mode+'&support_ids[]='+this.arr[0]+'&support_ids[]='+this.arr[1]).then(res=>{
                        console.log(res);
                        this.shopArr = res;
                        this.show1 = false;
                    }).catch(err=>{})
                }else if (this.arr.length==3){
                    //获取筛选店铺信息
                    this.axios.get("shopping/restaurants?latitude="+this.latitude+'&longitude='+this.longitude+'&delivery_mode[]='+this.delivery_mode+'&support_ids[]='+this.arr[0]+'&support_ids[]='+this.arr[1]+'&support_ids[]='+this.arr[2]).then(res=>{
                        console.log(res);
                        this.shopArr = res;
                        this.show1 = false;
                    }).catch(err=>{})
                }
                else if (this.arr.length==4){
                    //获取筛选店铺信息
                    this.axios.get("shopping/restaurants?latitude="+this.latitude+'&longitude='+this.longitude+'&delivery_mode[]='+this.delivery_mode+'&support_ids[]='+this.arr[0]+'&support_ids[]='+this.arr[1]+'&support_ids[]='+this.arr[2]+'&support_ids[]='+this.arr[3]).then(res=>{
                        console.log(res);
                        this.shopArr = res;
                        this.show1 = false;
                    }).catch(err=>{})
                } else if (this.arr.length==5){
                    //获取筛选店铺信息
                    this.axios.get("shopping/restaurants?latitude="+this.latitude+'&longitude='+this.longitude+'&delivery_mode[]='+this.delivery_mode+'&support_ids[]='+this.arr[0]+'&support_ids[]='+this.arr[1]+'&support_ids[]='+this.arr[2]+'&support_ids[]='+this.arr[3]+'&support_ids[]='+this.arr[4]).then(res=>{
                        console.log(res);
                        this.shopArr = res;
                        this.show1 = false;
                    }).catch(err=>{})
                }else if (this.arr.length==6){
                    //获取筛选店铺信息
                    this.axios.get("shopping/restaurants?latitude="+this.latitude+'&longitude='+this.longitude+'&delivery_mode[]='+this.delivery_mode+'&support_ids[]='+this.arr[0]+'&support_ids[]='+this.arr[1]+'&support_ids[]='+this.arr[2]+'&support_ids[]='+this.arr[3]+'&support_ids[]='+this.arr[4]+'&support_ids[]='+this.arr[5]).then(res=>{
                        console.log(res);
                        this.shopArr = res;
                        this.show1 = false;
                    }).catch(err=>{})
                }
                // this.arr = []
                this.show3 = false;
            }
            }
    }
</script>

<style scoped>
    #head_top{
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    .head_goback{
        left: .4rem;
        width: .6rem;
        height: 1rem;
        line-height: 2.2rem;
        margin-left: .4rem;
        color: #fff;
    }
    .title_head{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    .title_text {
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }
    .sort_container{
        background-color: #fff;
        border-bottom: .025rem solid #f1f1f1;
        position: fixed;
        top: 1.95rem;
        right: 0;
        width: 100%;
        display: flex;
        z-index: 13;
        box-sizing: border-box;
    }
    .sort_container .sort_item{
        font-size: .55rem;
        color: #444;
        width: 33.3%;
        height: 1.6rem;
        text-align: center;
        line-height: 1rem;
    }
    .sort_item .sort_item_container{
        width: 100%;
        height: 100%;
        position: relative;
        z-index: 14;
        background-color: #fff;
        box-sizing: border-box;
        padding-top: .3rem;
    }
    .food_container {
        padding-top: 3.6rem;
    }
    .sort_item_container .el-button {
        width: 100%;
        height: 1rem;
        border: none;
        line-height: .02rem;
        border-right: .025rem solid #e4e4e4;
    }
     .category_left{
        flex: 1;
        background-color: #f1f1f1;
        height: 16rem;
        overflow-y: auto;
        width: 100%;

    }
    .category_left_li {
        display: flex;
        justify-content: space-between;
        padding: 0 .5rem;
        padding-right: 170px
    }
    .category_left_li .category_icon{
        width: .8rem;
        height: .8rem;
        vertical-align: middle;
        margin-right: .2rem;
    }
    .category_left span {
        font-size: .5rem;
        color: #666;
        line-height: 1.8rem;
    }
     .category_left  .category_active {
        background-color: #fff;
    }
    .category_count{
        background-color: #ccc;
        font-size: .4rem;
        color: #fff;
        padding: 0 .1rem;
        border: .025rem solid #ccc;
        border-radius: .8rem;
        vertical-align: middle;
        margin-right: .25rem;
    }
    .category_left_li i {
        vertical-align: middle;
    }
   i{
        font-size: .5rem;
    }
    @keyframes change {
        0% {
            opacity: 1;
            transform: rotateZ(0deg);
        }
        100% {
            transform: rotateZ(180deg);
            opacity: 1;
        }
    }
    .sj-leave-active {
        animation: change 1s;
    }
    .sj-enter-active {
        animation: change 1s reverse;
    }
    .category_right .category_right_li{
        display: flex;
        justify-content: space-between;
        height: 1.8rem;
        line-height: 1.8rem;
        padding-right: .5rem;
        border-bottom: .025rem solid #e4e4e4;
    }
    .category_right{
        position: absolute;
        top: 47px;
